import React, { useEffect, useState } from 'react';
import { createCn } from 'bem-react-classname';
import { EventDuration } from '../event-duration/event-duration';
import { EventStart } from '../event-duration/event-start';
import { EventUpdate } from '../event-duration/event-update';

import './sub-event-row.scss';

const cn = createCn('sub-event-row');

type OwnProps = Partial<{
    event: any;
}>

export const SubEventRow = React.memo<OwnProps>((props) => {
    const { event } = props;
    const [now, setNow] = useState(new Date());

    useEffect(() => {
        setInterval(() => {
            const now = new Date();
            setNow(now);
        }, 5000)
    }, []);

    return (
        <div className={ cn() }>
            <div className={ cn('name') }>{ event.name }</div>
            <div className={ cn('update') }>
                { event.start_timer &&
                    <EventStart now={ now } start={ event.start_timer }/>
                }
            </div>
            <div className={ cn('timer', { expiring: event.expiring }) }>
                { event.end_timer &&
                    <EventDuration now={ now } duration={ event.end_timer }/>
                }
            </div>
            <div className={ cn('update') }>
                { event.repeatable?.value &&
                    <EventUpdate now={ now } repeatable={ event.repeatable } start={ event.start_timer }/>
                }
            </div>
        </div>
    );
});
