import React, { Fragment, useCallback, useEffect, useRef } from 'react';
import { createCn } from 'bem-react-classname';
import { useDispatch, useSelector } from 'react-redux';
import { teamsSelector } from '../../../redux/selectors';

import './my-teams.scss';
import * as thunks from '../../../redux/actions';

const cn = createCn('my-teams');

function isEmptyObject(obj: any) {
    return Object.keys(obj).length === 0;
}

export const MyTeams = (() => {
    const dispatch = useDispatch();
    const teams = useSelector(teamsSelector);
    const options = !isEmptyObject(teams) ? Object.keys(teams) : [];
    const ref = useRef(null);

    console.log(Object.keys(teams));

    const onSave = useCallback(
        () => {
            if (ref.current !== null) {
                // @ts-ignore
                const value = ref.current.value;
                console.log(value);
                const id = value.replace(/\s/g, '');
                const lowercaseId = id.toLowerCase();
                dispatch(thunks.onSaveTeam(lowercaseId));
            }
        },
        [dispatch]
    );

    const onLoad = useCallback(
        () => {
            if (ref.current !== null) {
                // @ts-ignore
                const value = ref.current.value;
                console.log(value);
                const id = value.replace(/\s/g, '');
                const lowercaseId = id.toLowerCase();
                dispatch(thunks.onSaveTeam(lowercaseId));
            }
        },
        [dispatch]
    );

    useEffect(() => {
    }, [])

    return (
        <div className={ cn() }>
            <div className={ cn('input') }>
                <input type="text" name={ 'new_team' } placeholder={ 'Team name' } ref={ ref }/>
            </div>
            <div className={ cn('save') }>
                <button onClick={ onSave }>Save</button>
            </div>
            { !isEmptyObject(teams) &&
                <Fragment>
                    <div className={ cn('hr') }/>
                    <div className={ cn('teams') }>
                        <select name="teams" id="teams">
                            { options.map((item) => {
                                return <option value={ item }>{ item }</option>
                            }) }
                        </select>
                    </div>
                    <br/>
                    <div className={ cn('load') }>
                        <button onClick={ onLoad }>Load</button>
                    </div>
                </Fragment>

            }
        </div>
    );
});
