import React from 'react';
import { differenceInHours } from 'date-fns';

import './event-duration.scss';
import intervalToDuration from 'date-fns/intervalToDuration';
import { addLeadingZero } from '../../utils/addLeadingZero';

type OwnProps = Partial<{
    now: any;
    start: any;
}>



export const EventStart = React.memo<OwnProps>((props) => {
    const { now, start } = props;
    const startDate = new Date(start);
    const interval = intervalToDuration({
        start: startDate,
        end: now
    });

    const nextRepeat = () => {
        return differenceInHours(startDate, now);
    };

    const formatTime = (time: any) => {
        const days = Math.floor(time / 24) + 1;

        if (time < 0) {
            return '';
        } else if (days < 2) {
            const days = interval.days && interval.days > 0 ? 24 : 0;
            const hours = addLeadingZero(interval.hours, days);
            const minutes = addLeadingZero(interval.minutes);
            const seconds = addLeadingZero(interval.seconds);

            return `${ hours }:${ minutes }:${ seconds }`;
        }
        else {
            return `${ days } Days`;
        }
    };

    return (
        <React.Fragment>
            { formatTime(nextRepeat()) }
        </React.Fragment>
    );
});
